<template>
    <div>  
        <div class="loginNav">
            <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">家长私信</p>
            <span class="f26 cfff pr-30" style="position:absolute;top:0;right:0;line-height:0.84rem;z-index:99;" @click="$router.push({path:'/writeLetter'})">写信</span>
        </div>
        <div style="padding:0.84rem 0 1.7rem;">
            <div class="pl-30">
                <ul>
                    <li class="pr-30 b_line" v-for="(item,index) in list" :key="index" @click="lookDetail(index)">
                        <div>
                            <p class="f28 c333">{{item.name}}</p>
                            <img :style="{display:item.isSee?'none':'inline-block'}" src="../../assets/message/notSee.png" alt="">
                        </div>
                        <div class="mt-22">
                            <p class="f26 c666" style="overflow:hidden;text-overflow:ellipsis;white-space:nowrap;width:80%;">{{item.reason}}</p>
                            <p class="f22 c999">{{item.time}}</p> 
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            list:[
                {
                    name:'爱心捐款',
                    isSee:false,
                    reason:'我们班肖玉芳同学于上月体检中检查出...',
                    time:'2018-06-09'
                },{
                    name:'夏令营通知',
                    isSee:false,
                    reason:'全校将于下月20号组织全校师生进行夏...',
                    time:'2018-06-09'
                },{
                    name:'今日任务',
                    isSee:true,
                    reason:'今日家庭作业主要是回家帮助父母承担...',
                    time:'2018-06-09'
                },{
                    name:'期末考试安排',
                    isSee:true,
                    reason:'全校将于下月20号组织全校师生进行夏...',
                    time:'2018-06-09'
                },
            ]
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        not(){
            this.active=true
            this.active1=false
        },
        yes(){
            this.active=false
            this.active1=true
        },
        lookDetail(index){
            var list=this.list;
            list[index].isSee=true;
            this.list=list;
            this.$router.push({path:'/letterDetail'})
        },
    }
}
</script>

<style scoped>
     /* 修改组件样式 */
    .loginNav{
        position: fixed;
    }
    .van-tabbar-item--active {color: #8a9cff;}
    
    li{
        padding:0.3rem 0.3rem 0.2rem 0;
    }
    li div{
        display: flex;
        justify-content: space-between;
        align-items: center
    }
    li img{
        width:0.14rem;
        height:0.14rem;
    }
</style>




